import process from "process"
import { LogTail } from "../core/log/LogTail"
import { compactBlanks } from "../fe/Array"
import { ServiceNames } from "../fe/ServiceNames"
import { CLI } from "./cli/CLI"
import { ColorArgs } from "./cli/ColorArgs"
import { logDir } from "./dir/LogDir"

async function run() {
  const defaultLogDir = logDir()
  const cli = new CLI(
    ServiceNames.logtail,
    "[LOG_DIR]",
    "Normally logtail watches all files in the default logging directory, " +
      defaultLogDir +
      ". Specify LOG_DIR or set the PS_LOG_DIR environment variable to override this directory."
  )
    .add(ColorArgs)
    .parse()
  process.on("SIGINT", () => process.exit(0))
  process.on("SIGTERM", () => process.exit(0))
  const args = compactBlanks(cli.args)
  new LogTail(args[0] ?? defaultLogDir)
}

void run()
