import os from "os"
import { lazy } from "../../core/Lazy"
import { minuteMs } from "../../fe/Date"
import { gt0 } from "../../fe/Number"
import { mkLogger } from "../Logger"
import { intFromFileSync } from "../fs/ReadFile"
import { min } from "../math/Vector"
import { isLinux } from "../platform/Platform"

const logger = lazy(() => mkLogger("work.CpuInfo"))

// cpus() can take 500ms!
export const cpuInfo = lazy(() => os.cpus(), minuteMs)

export const cpuCount = lazy(() => {
  return min([sysFsCpu(), availableParallelism(), cpuInfo().length]) ?? 1 // < hope we never need that fallback!
}, minuteMs)

function availableParallelism() {
  return logger().tap({
    msg: "os.availableParallelism()",
    result: os.availableParallelism?.()
  })
}

const SysFsCgroupCpuQuota = "/sys/fs/cgroup/cpu/cpu.cfs_quota_us"
const SysFsCgroupCpuPeriod = "/sys/fs/cgroup/cpu/cpu.cfs_period_us"
const SysFsCgroupCpuShares = "/sys/fs/cgroup/cpu/cpu.shares"

const sysFsCpu = lazy(() => {
  if (!isLinux) return

  // Are we in a pod?
  // See https://www.reddit.com/r/PhotoStructure/comments/sn68f9/initial_scan_not_adding_everything/hw4bqmj/
  const quota = intFromFileSync(SysFsCgroupCpuQuota)
  const period =
    quota != null ? intFromFileSync(SysFsCgroupCpuPeriod) : undefined
  if (gt0(quota) && gt0(period)) {
    return logger().tap({
      msg: "sysFsCpu(): valid /sys/fs quota values found",
      level: "info",
      result: quota / period,
      meta: {
        quota,
        period,
        quotaPath: SysFsCgroupCpuQuota,
        periodPath: SysFsCgroupCpuPeriod
      }
    })
  }

  const shares = intFromFileSync(SysFsCgroupCpuShares)
  if (gt0(shares)) {
    return logger().tap({
      msg: "sysFsCpu(): valid value in " + SysFsCgroupCpuShares,
      level: "info",
      result: (cpuInfo().length * shares) / 1024,
      meta: { shares, sharePath: SysFsCgroupCpuShares }
    })
  }
  return
})
